import React, {useState} from "react";

const SearchBar =({ onSearch }) =>{
    const [query, setQuery] = useState("");

    const handleSearch = ()=>{
        onSearch(query);
    };

    return (
      <div style={{ textAlign: "center", margin: "20px" }}>
        <input
          type="text"
          placeholder="Search..."
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          style={{
            padding: "10px 15px",
            width: "300px",
            fontSize: "16px",
            border: "1px solid #ccc",
            borderRadius: "8px", // Membuat sudut membulat
            outline: "none",
            transition: "all 0.3s ease", // Transisi halus
            boxShadow: "0px 2px 4px rgba(0, 0, 0, 0.1)", // Memberikan bayangan
          }}
          onFocus={(e) => (e.target.style.borderColor = "#001F54")} // Biru dongker saat fokus
          onBlur={(e) => (e.target.style.borderColor = "#ccc")}
        />
        <button
          onClick={handleSearch}
          style={{
            padding: "10px 20px",
            marginLeft: "10px",
            backgroundColor: "#001F54", // Biru dongker
            color: "white",
            border: "none",
            borderRadius: "8px", // Membuat tombol membulat
            fontSize: "16px",
            cursor: "pointer",
            transition: "all 0.3s ease", // Transisi halus
          }}
          onMouseEnter={(e) => (e.target.style.backgroundColor = "#00296B")} // Lebih terang saat hover
          onMouseLeave={(e) => (e.target.style.backgroundColor = "#001F54")}
        >
          Search
        </button>
      </div>

    
    );
};
export default SearchBar;
